import React, { useState, useEffect } from "react";
import {
  Overlay,
  ModalContainer,
  ModalTitle,
  CloseButton,
  DropArea,
  ModalBtn,
  ButtonGroup,
} from "../../Styles/DataRoomStyle.js";
import axios from "axios";
const AiQuestionForm = ({
  onClose,
  catgeoryId,
  subcatgeoryId,
  CategorynameFile,
  refreshpageAi,
  Docname,
  DeleteIdDocs,
  AIquestions,
}) => {
  var apiURLAiFile = "https://blueprintcatalyst.com/api/user/aifile/";
  const storedUsername = localStorage.getItem("UserLoginData");
  const userLogin = JSON.parse(storedUsername);
  const [selectedFiles, setSelectedFiles] = useState([]);
  const [errMessage, seterrMessage] = useState("");
  const [spinners, setspinners] = useState(false);
  const [filenamecheck, setfilenamecheck] = useState(Docname);
  const [loopAiquestion, setloopAiquestion] = useState(AIquestions);
  const [answers, setAnswers] = useState({});
  const [successresponse, setsuccessresponse] = useState(false);
  useEffect(() => {
    if (loopAiquestion && loopAiquestion.length > 0) {
      const initialAnswers = {};
      loopAiquestion.forEach((q) => {
        initialAnswers[q.id] = q.answer || ""; // pre-fill if answer already exists
      });
      setAnswers(initialAnswers);
    }
  }, [loopAiquestion]);
  const handleAnswerChange = (id, value) => {
    setAnswers((prev) => ({
      ...prev,
      [id]: value,
    }));
  };
  const handleSubmit = async (e) => {
    e.preventDefault();

    const payload = loopAiquestion.map((q) => ({
      questionId: q.id,
      question: q.questions,
      answer: answers[q.id] || "",
      user_id: userLogin.id,
      dataroomdocuments_id: q.dataroomdocuments_id, // assuming this exists
    }));
    try {
      await axios.post(apiURLAiFile + "RespoonseAIquestion", payload, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      setsuccessresponse(true);
      setTimeout(() => {
        refreshpageAi();
        setsuccessresponse(false);
      }, 2000);
    } catch (err) {
      console.error("Submit error:", err);
    }
  };

  return (
    <Overlay>
      <ModalContainer>
        <form onSubmit={handleSubmit} method="post" action="javascript:void(0)">
          <CloseButton onClick={onClose}>×</CloseButton>
          <div className="formmodal">
            <ModalTitle className="aititle">AI Question</ModalTitle>
            {loopAiquestion.length === 0 && <span>No AI Questions</span>}

            {loopAiquestion.map((q, index) => (
              <div key={q.id} className="form-group mb-3">
                <label>Question {index + 1}</label>
                <input
                  type="text"
                  className="form-control mb-2"
                  value={q.questions}
                  readOnly
                />

                <textarea
                  className="form-control"
                  rows="3"
                  placeholder="Enter your answer..."
                  value={answers[q.id] || ""}
                  onChange={(e) => handleAnswerChange(q.id, e.target.value)}
                  required
                />
              </div>
            ))}

            {errMessage && (
              <span className="text-danger fileerror">{errMessage}</span>
            )}
            {successresponse && (
              <span className="text-success fileerror">
                Updated successfully
              </span>
            )}
          </div>
          {loopAiquestion.length > 0 && (
            <ButtonGroup>
              <ModalBtn onClick={onClose}>Close</ModalBtn>
              <ModalBtn
                disabled={spinners}
                variant="upload"
                type="submit"
                style={{ opacity: spinners ? 0.6 : 1 }}
                className="submit d-flex align-items-center gap-2"
              >
                Save
                {spinners && (
                  <div
                    className="white-spinner spinner-border spinneronetimepay m-0"
                    role="status"
                  >
                    <span className="visually-hidden"></span>
                  </div>
                )}
              </ModalBtn>
            </ButtonGroup>
          )}
        </form>
      </ModalContainer>
    </Overlay>
  );
};

export default AiQuestionForm;
